import React, { useEffect } from "react";
import Header from "../commonComponent/header";
import SideBar from "../commonComponent/sideBar";
import UserSubscriptionListing from "./userSubscriptionListing";
import SubscriptionListing from "./subscriptionListing";
import { Link, useLocation, useNavigate } from "react-router-dom";

function Subscription() {
  const location = useLocation();
  const navigate = useNavigate();

  const queryParams = new URLSearchParams(location.search);
  const activeTab = queryParams.get("tab") || 1;

  useEffect(() => {
    if (activeTab == 2) {
      const allTabs = document.querySelectorAll(".tab-pane");
      const allTabLinks = document.querySelectorAll(".nav-item");

      allTabs.forEach((tab) => tab.classList.remove("show", "active"));
      allTabLinks.forEach((link) => link.classList.remove("active"));

      document
        .getElementById("merchant-subscription-plan-tab")
        .classList.add("active");
      document
        .getElementById("merchant-subscription-plan")
        .classList.add("show", "active");
    }
  }, [activeTab]);

  const handleTab = (tabId) => {
    queryParams.set("tab", tabId);
    queryParams.delete("page");
    navigate({ search: queryParams.toString() }, { replace: true });
  };
  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />

        <div className="admin_contentpart">
          <div className="d-flex justify-content-between">
            <div
              className="tabs nav nav-tabs common_tabs"
              id="reportsTab"
              role="tablist"
            >
              <label
                className="tab nav-item text-capitalize active"
                htmlFor="radio-1"
                id="subscription-plan-tab"
                data-bs-toggle="tab"
                href="#subscription-plan"
                role="tab"
                aria-controls="subscription-plan"
                aria-selected="true"
                onClick={() => handleTab(1)}
              >
                Subscription Plan
              </label>
              <label
                className="tab nav-item text-capitalize"
                htmlFor="radio-2"
                id="merchant-subscription-plan-tab"
                data-bs-toggle="tab"
                href="#merchant-subscription-plan"
                role="tab"
                aria-controls="merchant-subscription-plan"
                aria-selected="false"
                onClick={() => handleTab(2)}
              >
                Users Subscription Plan
              </label>
            </div>
            <div className="d-flex">
              {activeTab == 2 ? (
                <div className="comman_btn w-fit">Enable Subscription Plan</div>
              ) : (
                <Link
                  to="/admin/add-subscription-plan"
                  className="comman_btn w-fit ms-2"
                >
                  Add Subscription Plan
                </Link>
              )}
            </div>
          </div>
          <div className="row comman_design mx-0 mt-4">
            <div className="tab-content mt-4" id="reportsTabContent">
              <div
                className="tab-pane common_table fade show active"
                id="subscription-plan"
                role="tabpanel"
                aria-labelledby="subscription-plan-tab"
              >
                <SubscriptionListing />
              </div>

              <div
                className="tab-pane common_table fade"
                id="merchant-subscription-plan"
                role="tabpanel"
                aria-labelledby="merchant-subscription-plan-tab"
              >
                <UserSubscriptionListing />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Subscription;
